}

body.show app-color-switcher, body.show app-app > div, body.show app-user > div {
  opacity: 1;
}

.rtl * {
  text-align: right;
}

.fixed-background {
  background: url("../../../bdalumni/img/balloon-lg.jpg") no-repeat center center fixed;
  background-size: cover;
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

.disable-text-selection {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.scroll {
